import common from '../../common/common.js'

export default {
	data() {
		return {
			baseUrl: common.baseUrl,
			userid: '',
			userInfo: [],
			info: {
				bg_color: '255,255,255',
				bg_alpha: 1,
				delta: 1,
			},
			scrollTop: 0, //滚动高度
			header_bg_alpha: 0, //头部背景色透明度
			currentPagesLen: 1, //页面数量，看是否返回
			titleHeight: 0,
			statusBarHeight: 0,
			naviBarHeight: 0,

			adsList: [], //特推商品列表
			isall: 0, //是否显示完
			page: 0, //当前页码
		};
	},
	onLoad(e) {
		var that = this
		var userid = uni.getStorageSync('userid');
		var userInfo = uni.getStorageSync('userInfo');
		console.log(userInfo)

		that.userid = userid
		that.userInfo = userInfo

		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;

		that.getSuperAdsList();


	},
	onPageScroll(e) {
		let that = this
		that.scrollTop = e.scrollTop
		if (e.scrollTop < 100) {
			that.header_bg_alpha = e.scrollTop / 100
		} else {
			that.header_bg_alpha = 1
		}
		console.log(that.header_bg_alpha)
	},

	onPullDownRefresh: function() {
		var that = this
		uni.showNavigationBarLoading();

		that.resetAttr();
		that.getSuperAdsList();

		console.log('下拉刷新中...');
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') { // 来自页面内转发按钮
			console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		if (userInfo.users_id > 0) {
			var shareUrl = '/pages/super/super?tjuid=' + userInfo.users_id
		} else {
			var shareUrl = '/pages/super/super'
		}
		return {
			title: '商家活动广告平台——众晨星',
			path: shareUrl,
		}
	},
	onReachBottom() {
		var that = this
		that.loadmore()
	},
	methods: {

		//重置参数
		resetAttr() {
			var that = this
			that.page = 0;
			that.isall = 0;
			that.adsList = []
		},
		//加载更多
		loadmore() {
			var that = this
			that.page = that.page + 1;
			that.getSuperAdsList();
		},
		//获取特推活动列表
		getSuperAdsList() {
			var that = this
			var lng = uni.getStorageSync('lng');
			var lat = uni.getStorageSync('lat');
			uni.request({
				url: common.apiUrl + 'shop/getSuperAdsList',
				method: 'GET',
				data: {
					lng: lng,
					lat: lat,
					page: that.page + 1
				},
				success: res => {
					var data = res.data
					console.log(data)
					var adsList = data.ads_list
					for (var i = 0; i < adsList.length; i++) {
						that.adsList.push(adsList[i])
					}
					that.isall = data.isall
					console.log("isall", data.isall)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//电话联系
		phoneContact(e) {
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.commendShopList
			var mobile = e.currentTarget.dataset.mobile
			uni.makePhoneCall({
				phoneNumber: mobile
			});
		},
		//一键导航
		goaddr(e) {
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.shopList
			var lat = shopList[index].shop_lat
			var lng = shopList[index].shop_lng

			console.log(parseFloat(lat));
			console.log(parseFloat(lng));
			uni.openLocation({
				latitude: parseFloat(lat),
				longitude: parseFloat(lng),
				success: function() {
					console.log('success');
				}
			});
		},
		navBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		navHome() {
			uni.switchTab({
				url: '/pages/index/index'
			})
		},
		//跳转
		jumpUrl(e) {
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url: url,
			});
		},
		//获取用户信息
		getUserInfo() {
			var that = this
			var userid = that.userid
			if (userid > 0) {
				uni.request({
					url: common.apiUrl + 'user/getUserInfo',
					method: 'GET',
					data: {
						userid: userid
					},
					success: res => {
						if (res.data.code == 0) {
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo', userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
	}
}
